*{
    font-family: 'Times New Roman', Times, serif;
    font-size: 18px;

}
body{
    display: flex;
    justify-content: center;
    align-items: center;
}
.resultat{
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
    text-align: center;
    margin: 0 auto;
    width: 260px;
    border: 10px solid black;
    background: black;
    border-radius: 5px;
}
.keypad{
    display: grid;

    grid-template-columns: repeat(4,1fr);
    grid-auto-rows: minmax(60px,auto);

}
input{
    border: 0px;
    height: 75px;
    width: 255px;
    background-color: black ;
    color: beige;
    text-align: right;
    font-size: 25px;
    font-weight: 500;
    letter-spacing: 1px;

}
#clear{
    grid-column: 1/3;
    color: #262834;


}
#egual{
    grid-column: 3/5;
    grid-row: 5;
    color: #262834;

}

button{
    margin: 5px;
    border: none;
    border-radius: 5px;
    background-color: #262834;
    color: #ffff;
    font-weight: 500;

}
#bb{
    color: #262834;
}
input:focus,
input:active,
button:focus,
button:active{
    outline: none;
}
.highlight{
    background-color: #00eeff;
}


